body {
    margin: 0;
    padding: 0;
}

.flex {
    display: flex;
}

.items-center {
align-items: center;
}

.items-start {
align-items: flex-start;
}

.items-end {
align-items: flex-end;
}

.justify-start {
justify-content: flex-start;
}

.justify-center {
justify-content: center;
}

.justify-between {
justify-content: space-between;
}

.justify-around {
justify-content: space-around;
}

.justify-evenly {
justify-content: space-evenly;
}

.justify-end {
justify-content: flex-end;
}

.flex-col {
flex-direction: column;
}

.flex-wrap {
flex-wrap: wrap;
}

.col-gap {
column-gap: 0.75rem;
}

.col-gap-s {
column-gap: 0.5rem;
}

.row-gap {
row-gap: 1.25rem;
}

.row-gap-s {
row-gap: 0.75rem;
}

.w-full {
    width: 100%;
}

.h-full {
height: 100%;
}

.w-screen {
width: 100vw;
}

.h-screen {
height: 100vh;
}

.w-half {
width: 50%;
}

.h-half {
height: 50%;
}

.grid {
    display: grid;
}

.rounded-full {
    border-radius: 50%;
}