.intro {
  font-size: 3.5rem;
  color: #fff;
  line-height: 1.6;
}

.title {
  margin: 1.5rem;
}

.description {
  font-size: 2rem;
  width: 80%;
}

.btn {
  margin: 10px;
  padding: 1.8rem 3.5rem;
  background-color: #fff;
  color: #333;
  border: none;
  border-radius: 1rem;
  font-size: 1.5rem;
}

.btn:hover {
  -webkit-transform: translateY(-3px);
  transform: translateY(-3px);
}
